/* App.css */

/* Basic markdown styling */
#preview h1, #preview h2 {
  font-size: 2em;
  font-weight: bold;
}

#preview p {
  margin: 10px 0;
}

#preview code {
  background-color: #f5f5f5;
  padding: 2px 4px;
  font-family: monospace;
}

#preview pre {
  background-color: #f5f5f5;
  padding: 10px;
  white-space: pre-wrap;
  font-family: monospace;
}

#preview ul {
  margin: 10px 0;
}

#preview blockquote {
  background-color: #f9f9f9;
  padding: 10px;
  border-left: 5px solid #ccc;
}

#preview a {
  color: #007bff;
}

#preview img {
  max-width: 100%;
}
